import React from 'react'
import uuid from 'uuid/dist/v4'
import { Tabs, Table, Divider } from 'antd'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { TabPane } = Tabs;

const testData = [
  {
    token: 'xxx',
    TVL: 'xxxx',
    APY: 'xx%'
  }
]

const Info = (props) => {

  const columns = [{
    title: 'token',
    dataIndex: 'token',
    width: '20%'
  }, {
    title: 'TVL',
    dataIndex: 'TVL',
    width: '20%'
  }, {
    title: '机枪池收益',
    dataIndex: 'pool',
    width: '20%'
  }, {
    title: '跨链桥',
    dataIndex: 'bridge',
    width: '20%',
  }, {
    title: '跨链收益',
    dataIndex: 'bridge',
    width: '20%',
  }]

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.info}`}>
        <Tabs size="large">
          <TabPane tab="总览" key="all" />
          <TabPane tab="ETH" key="ETH" />
          <TabPane tab="HECO" key="HECO" />
          <TabPane tab="BSC" key="BSC" />
        </Tabs>
        <Table 
          rowKey={() => uuid()}
          columns={columns}
          dataSource={testData}
          pagination={false}
        />
      </div>
    </>
  )
}

export default Info
