import React from 'react'

import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const HomePage = (props) => {
  return (
    <>
      <GlobalHeader {...props}/>
      <div className={globalStyles.mainContent}>
        <div className={styles.info}>
          <div className={styles.infoLabel}>总锁仓：</div>
          <div className={styles.infoValue}>1230</div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>机枪池累计收益：</div>
          <div className={styles.infoValue}>1230</div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>跨链桥累计收益：</div>
          <div className={styles.infoValue}>1230</div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>平台币流通总量：</div>
          <div className={styles.infoValue}>1230</div>
        </div>
        <div className={styles.githubLink}>相关链接：<a href="https://github.twitter.telegram" target="_blank">github.twitter.telegram</a></div>
      </div>
    </>
  )
}

export default HomePage
