const baseSize = 37.5;

function setRem () {
    const scale = document.documentElement.clientWidth / 750;
    // 设置页面根节点字体大小

    document.documentElement.style.fontSize = 2 * baseSize * Math.min(scale, 2) + 'px';

}
// 初始化
setRem();
// 改变窗口大小时重新设置 rem
window.onresize = function () {
    setRem();
};