import React from 'react'
import uuid from 'uuid/dist/v4'
import { Tabs, Table, Divider, Button } from 'antd'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { TabPane } = Tabs;

const testData = [
  {
    LP: 'CUSDT',
    TVL: 'xxxx',
    APY: 'xx%'
  }
]

const Dividendpool = (props) => {

  const columns = [{
    title: 'LP',
    dataIndex: 'LP',
    width: '30%'
  }, {
    title: 'TVL',
    dataIndex: 'TVL',
    width: '30%'
  }, {
    title: 'APY',
    dataIndex: 'APY',
    width: '30%'
  }, {
    title: '操作',
    dataIndex: 'operation',
    width: '10%',
    render: (_, record, index) => {
      return (
        <div className={styles.group}>
          <div>质押</div>
          <Divider type="vertical" />
          <div>解押</div>
        </div>
      )
    }
  }]

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.dividendpool}`}>
        <Tabs size="large">
          <TabPane tab="ETH" key="ETH" />
          <TabPane tab="HECO" key="HECO" />
          <TabPane tab="BSC" key="BSC" />
        </Tabs>
        <div className={styles.info}>
          <div className={styles.infoLabel}>总质押：</div>
          <div className={styles.infoValue}>xxxx</div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoLabel}>我的质押：</div>
          <div className={styles.infoValue}>xxxx</div>
        </div>
        <div className={styles.info}>
          <Button type="primary" size='large'>质押</Button>
          <Button type="primary" size='large'>解押</Button>
          <Button type="primary" size='large'>提取收益</Button>
        </div>
        <Table 
          rowKey={() => uuid()}
          columns={columns}
          dataSource={testData}
          pagination={false}
        />
      </div>
    </>
  )
}

export default Dividendpool
