import React, { useState, useMemo } from 'react'
import uuid from 'uuid/dist/v4'
import { useWeb3React } from '@web3-react/core'
import { Tabs, Table, Divider, Modal, Input, message } from 'antd'
import { HecoContract, OecContract, HecoProvider, OecProvider, erc20abi, hecoAddr, oecAddr } from '../../../public/Web3'
import { signCall } from '../../utils/ethRequest'
import { toBN, injected, BN_to_str, verifyChainId, verifyAllown } from '../../utils/utils'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { TabPane } = Tabs;



const AmountModal = ({
  visible,
  type,
  item,
  provider,
  handleOk,
  handleCancel
}) => {
  const [amount, setAmount] = useState()
  const [balance, setBalance] = useState(null)
  const [error, setError] = useState(false)

  useMemo(() => {
    if (!item || !type) return;
    const { token, liquidity_token, token_decimals, liquidity_decimals } = item;
    const ethereum = window.ethereum
    const cubeContract = type === 'save' ? new provider.eth.Contract(erc20abi, token) : new provider.eth.Contract(erc20abi, liquidity_token);
    const decimals = type === 'save' ? token_decimals : liquidity_decimals
    cubeContract.methods.balanceOf(ethereum.selectedAddress).call().then(result => {
      const balance = BN_to_str(result, decimals)
      setBalance(balance)
    })

  }, [item, type])

  const handleChange = (value) => {
    setError(false)
    if (Number(value) > Number(balance)) return setError(true)
    setAmount(Number(value))
  }
  
  const onOk = () => {
    if (!amount) return handleCancel()
    handleOk(amount)
  }

  return (
    <Modal
      visible={visible}
      onOk={onOk}
      onCancel={() => {
        setAmount('')
        setError(false) 
        handleCancel()
      }}
      getContainer={false}
      closable={false}
    >
      <div className={styles.amount}>
        <Input value={amount} onChange={(e) => { handleChange(e.target.value) }} />
        <div onClick={() => { handleChange(balance) }}>剩余：{balance}</div>
        {
          error ? <div className={styles.errorTip}>输入数量不能大于剩余数量</div> : null
        }
      </div>
      
    </Modal>
  )
}

const Gunpool = (props) => {
  const { activate, active } = useWeb3React()

  const [visible, setVisible] = useState(false)
  const [item, setItem] = useState(null)
  const [type, setType] = useState('')
  const [ctrlFlag, setCtrlFlag] = useState(true)
  const [tabKey, setTabKey] = useState('HECO');
  const [tableData, setTableData] = useState([])

  const { myContract, myProvider, router_addr } = useMemo(() => {
    let contract;
    let provider;
    let addr;
    switch (tabKey) {
      case 'HECO':
        contract = HecoContract
        provider = HecoProvider
        addr = hecoAddr
        break;
      case 'OEC':
        contract = OecContract
        provider = OecProvider
        addr = oecAddr
        break;
      default:
        contract = HecoContract
        provider = HecoProvider
        addr = hecoAddr
        break;
    }
    return { myContract: contract, myProvider: provider, router_addr: addr }
  }, [tabKey])

  useMemo(() => {

    if (!ctrlFlag) return
    myContract.methods.getPoolInfos().call().then((result) => {
      const temp = result.map(item => {
        const { token,equity, symbol,liquidity_decimals, apy, token_decimals, liquidity_token  } = item;
        return {
          token,
          equity,
          symbol,
          liquidity_decimals,
          apy,
          token_decimals,
          liquidity_token
        }
      })
   
      setTableData(temp)
      setCtrlFlag(false)
    })
  }, [tabKey, ctrlFlag])

  const columns = [{
    title: 'token',
    dataIndex: 'symbol',
    width: '30%'
  }, {
    title: 'TVL',
    dataIndex: 'equity',
    width: '30%',
    render: (num, record) => BN_to_str(num, record.token_decimals)
  }, {
    title: 'APY',
    dataIndex: 'apy',
    width: '30%',
    render: text => `${(text / 100).toFixed(2)}%`
  }, {
    title: '操作',
    dataIndex: 'operation',
    width: '10%',
    render: (_, record, index) => {
      return (
        <div className={styles.group}>
          <div onClick={() => {handleCtrl(record, 'save')}}>存</div>
          <Divider type="vertical" />
          <div onClick={() => {handleCtrl(record, 'take')}}>取</div>
        </div>
      )
    }
  }]

  const handleCtrl = async (item, type) => {
    if (!active) {
      await activate(injected, walletError => {
        if (walletError.message.includes('user_canceled')) {
          return alert('You canceled the operation, please refresh and try to reauthorize.')
        }
        alert(`Failed to connect: ${walletError.message}`)
      })
    }
    
    

    const { token, liquidity_token } = item
    const ethereum = window.ethereum
    const typeToken = type === 'save' ? token : liquidity_token
    const ercCntr = new myProvider.eth.Contract(erc20abi, typeToken)
    const allown = await verifyAllown(ercCntr, ethereum.selectedAddress, router_addr)
    const vaildChainId = await verifyChainId(myProvider, ethereum)

    if (!vaildChainId) {
      return message.error('请切换网络')
    }


    if (allown === '0') {
      return signCall(
        ethereum,
        typeToken,
        ercCntr.methods.approve(router_addr,toBN()).encodeABI()
      )
    }

    setItem(item)
    setType(type)
    setVisible(true)
    
    
  }

  const handleOk = async (money) => {
    const { token, token_decimals } = item
    const ethereum = window.ethereum
    const amount = toBN(money, token_decimals)

    const data = type === 'save' ? myContract.methods.deposit(token, amount).encodeABI() : myContract.methods.withdraw(token, amount).encodeABI()

    ctrlMoney(ethereum, router_addr, data).then(result => {
      if (result) {
        message.success('交易成功')
        setTimeout(() => {
          setCtrlFlag(true)
        }, 5000);
        setVisible(false)
      }
    })
  }

  const ctrlMoney = (eth, addr,data ) => {
    return signCall(eth,addr,data)
  }

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.gunpool}`}>
        <Tabs
          size="large"
          activeKey={tabKey}
          onChange={key => {
            setTabKey(key)
            setCtrlFlag(true)
          }}
        >
          <TabPane tab="HECO" key="HECO" />
          <TabPane tab="OEC" key="OEC" />
          <TabPane tab="ETH" key="ETH" />
          <TabPane tab="BSC" key="BSC" />
        </Tabs>
        <Table 
          rowKey={() => uuid()}
          columns={columns}
          dataSource={tableData}
          pagination={false}
        />
      </div>
      <AmountModal
        visible={visible}
        type={type}
        item={item}
        provider={myProvider}
        addr={router_addr}
        handleOk={handleOk}
        handleCancel={() => {setVisible(false)}}
      />
    </>
  )
}

export default Gunpool
