import React, { useMemo } from 'react'
import classnames from 'classnames'
import uuid from 'uuid/dist/v4'
import { injected } from '../../utils/utils'
import { useWeb3React } from '@web3-react/core'
import { Link } from 'react-router-dom'
import styles from './index.less'

const router = [{
  path: '/home',
  name: '首页'
}, {
  path: '/gunpool',
  name: '机枪池'
}, {
  path: '/bridge',
  name: '跨链桥'
}, {
  path: '/pledgepool',
  name: '质押池'
}, {
  path: '/dividendpool',
  name: '分红池'
}, {
  path: '/info',
  name: '信息'
}]

const Header = ({
  location: {
    pathname
  }
}) => {
  const { activate, active, account } = useWeb3React()

  const handleContect = async () => {
    await activate(injected, walletError => {
      if (walletError.message.includes('user_canceled')) {
        return alert('You canceled the operation, please refresh and try to reauthorize.')
      }
      alert(`Failed to connect: ${walletError.message}`)
    })
  }

  const userInfo = useMemo(() => {
    if (!active) return (<div className={styles.connect} onClick={handleContect}>链接</div>);
    return `账号：${account.replace(0, 10, '...')}`
  }, [active, account])

  

  return (
    <div className={styles.headerWrapper}>
      <div className={styles.logo}>logo</div>
      <div className={styles.menu}>
        {
          router.map(item => <Link className={classnames({
            [styles.active]: item.path === pathname
          })} key={uuid()} to={item.path}>{item.name}</Link>)
        }
      </div>
      <div className={styles.user}>{userInfo}</div>
    </div>
  )
}

export default Header
