import React, { useMemo, useState } from 'react'
import { Button } from 'antd'
import { InjectedConnector } from '@web3-react/injected-connector'
import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'

const injected = new InjectedConnector({
  supportedChainIds: [
    1, // Mainet
    3, // Ropsten
    4, // Rinkeby
    5, // Goerli
    42, // Kovan
    66, // 
    256, //hecotest
  ],
})

const Test = () => {
  const { activate, active, account, library } = useWeb3React()
  const [ result, setResult] = useState('')

  const info = useMemo(() => {
    if (!active) return 'no connect';
    return `账号：${account}`
  }, [active, account])

  const handleContect = async () => {
    await activate(injected, walletError => {
      if (walletError.message.includes('user_canceled')) {
        return alert('You canceled the operation, please refresh and try to reauthorize.')
      }
      alert(`Failed to connect: ${walletError.message}`)
    })
  }

  const handleSign = async () => {
    const signer = library.getSigner(account)
    try {
      const result = await signer.signMessage('imToken')
      setResult(result)
    } catch (err) {
      if (err?.errorCode === 1001) {
        return alert('You canceled the operation.')
      }
      alert(err.message)
    }
    console.log('account', account)
  }

  return (
    <div>
      <div>info: {info}</div>
      <div>签名：{result} </div>
      <Button type="primary" onClick={handleContect}>链接</Button>
      <Button type="primary" onClick={handleSign}>签名</Button>
    </div>
  )
}

export default Test
